import { getUserEvents } from '@/request/api/focus';
import { mapMutations } from 'vuex';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 用户昵称
      nickname: '',
      // 各种类型的动态
      events: []
    };
  },

  async mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.id = this.$route.query.id;
    this.nickname = this.$route.query.nickname;
    this.limit = this.$route.query.limit; // console.log(this.id, this.nickname, this.limit);
    // 获取用户动态

    const res = await getUserEvents(this.id, this.limit, sessionStorage.getItem("cookie")); // console.log('获取用户动态', res);

    this.events = res.data.events; // console.log('用户动态', this.events);

    if (this.events.length > 0) this.isLoading = 0;
  },

  methods: {
    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    },

    // 点击动态
    eventClick(item) {
      // 播放音乐
      if (item.type == 18) {
        // console.log(item);
        // 先从 json 字符串中获取歌曲信息
        // console.log(this.formatJson(item.json).song);
        // 临时播放列表(只有点击动态的这一首歌)
        let tempSongs = [];
        tempSongs.push(this.formatJson(item.json).song); // console.log('临时播放列表 tempSongs', tempSongs);
        // 显示底部音乐播放组件

        this.updateIsPlayBox(1); // 更新播放列表

        this.updatePlayList(tempSongs); // 更新歌单名(歌曲来源)

        this.updateSongListName('动态'); // 更新歌单 id(歌曲来源)

        this.updateSongListId(0); // 更新播放列表下标

        this.updatePlayListIndex(0);
      } // 去往歌单详情页


      if (item.type == 13) {
        // console.log(item);
        // console.log(this.formatJson(item.json).playlist);
        this.$router.push({
          path: '/SongListDetail',
          query: {
            // 先从 json 字符串中获取歌单信息
            id: this.formatJson(item.json).playlist.id,
            // 1歌单 2专辑
            type: 1
          }
        });
      }
    },

    // 时间戳转日期
    formatPublishTime(time) {
      const date = new Date(time);
      const M = date.getMonth() + 1;
      const D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      const H = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      const Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(); // 获取当前时间

      const MNow = new Date().getMonth();
      const DNow = new Date().getDate(); // console.log(MNow + 1, DNow);
      // 当天发的动态

      if (M == MNow + 1 && D == DNow) return H + ':' + Min;else return M + '月' + D + '日 ' + H + ':' + Min;
    },

    // 把 json对象转换成 数组
    formatJson(json) {
      // JSON.parse() 把 json字符串 转化成 json 对象
      return JSON.parse(json);
    },

    // 对 description 的分段处理
    formatDescription(description) {
      let arr; // 以换行符分割每一段，每个 item 就是一段

      arr = description.split(/[(\r\n)\r\n]+/).map(item => item); // console.log('arr', arr);

      return arr;
    },

    ...mapMutations(['updateIsPlayBox', 'updatePlayList', 'updateSongListName', 'updateSongListId', 'updatePlayListIndex', 'updateDuration'])
  }
};